const tools = require('../../common/tools.js');
const server = require('../../common/server.js');
export default {
	data() {
		return {
			item: {},
			server: server.domain
		}
	},

	onLoad(options) {
		if (options.id) {
			this.id = options.id;
			this.getDetail()
		}
	},
	methods: {
		detail(id){
			tools.jumpTo("/pages/doctor/doctor?id="+id)
		},
		
		getDetail() {
			let that = this;
			let params = {
				id: 1,//that.id,
				pageSize:10,
				pageNumber:1
			};
			tools.request("/api/index/hospitalDetail.json", params).then(function(data) {
				that.item = data;
			})
		},
	}
}
